









































import { Component, Prop, Vue } from 'vue-property-decorator';
import axios from 'axios';

@Component
export default class Home extends Vue {

    bBox: string = '';
    styles: string = '';
    format: string = '';
    request: string = 'GetMap';
    version: string = '1.1.1';
    layers: string = '';
    width: number = 1280;
    height: number = 720;
    srs: string = 'EPSG:2170';

    allRequests = ['GetMap', 'GetCapabilities'];


    async onSubmit() {
        try {
            let res = await axios.get(`http://127.0.0.1:5000/geoserver/gis/wms`, {
                params: {
                    BBOX: this.bBox,
                    STYLES: this.styles,
                    FORMAT: this.format,
                    REQUEST: this.request,
                    VERSION: this.version,
                    LAYERS: this.layers,
                    WIDTH: this.width,
                    HEIGHT: this.height,
                    SRS: this.srs
                }
            })
            console.log(res);
        } catch (e) {
            console.log(e);
        }
    }
}
